import { pathToBase64, base64ToPath } from '@/js_sdk/gsq-image-tools/image-tools/index.js'
export default{
	data(){
		return{
			marchantId:null,//修改的id
			prevObj:{},
			bank:[],//支付
			bankIndex:0,
			takeP:[{proName:""}],//退货省
			tpIndex:0,
			takeC:[{proName:""}],//退货市
			tcIndex:0,
			takeX:[{proName:""}],//退货区
			txIndex:0,//
			bankP:[{proName:""}],//银行省
			bpIndex:0,
			bankC:[{proName:""}],//银行市
			bpCndex:0,
			takeContactsName:"",//收货联系人
			takeAddress:"",//收货地址
			takeContactsPhone:"",//收货联系电话
			businessLicence:"",//营业执照
			bSrc:"http://www.hnlxyj.com/wx/image/my/my_join_business_licence@2x.png",
			idCardB:"",//身份证反面
			ibSrc:"http://www.hnlxyj.com/wx/image/my/my_join_idback@2x.png",
			idCardA:"",//身份证正面
			iaSrc:"http://www.hnlxyj.com/wx/image/my/my_join_idside@2x.png",
			societyId:"",//社会信用统一编号
			bankEnrollName:"",//银行开户名
			mainBankNum:"",//银行帐号
			bankName:"",//开户支行
			checkNote:"",//留言
			isCheck:false,//是否同意协议
			shopObj:{}
		}
	},
	onNavigationBarButtonTap(e){
		uni.navigateTo({
			url:"service"
		})
		
	},
	methods:{
		chooseLocation(){
			uni.chooseLocation({
				success: function (res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
				}
			});
		},
		provinceList(obj){
			let that=this;
			that.$ajax({
				url:"/shopProduct/getCommonAddressProvinceList",
				success(d){
					that.takeP=d;
					that.bankP=d;
					if(obj){
						for(let i in d){
							if(obj.bankProvince==d[i].code){
								that.bpIndex=i;
								console.log(i)
								that.searchCity(1,"",obj)
							}
							if(obj.takeProvince==d[i].code){
								that.tpIndex=i;
								console.log(i)
								that.searchCity(2,"",obj)
							}
						}
					}else{
						that.searchCity()
					}
					
				}
			})
		},
		checkPhone(phone){
			if(!(/^1[3456789]\d{9}$/.test(phone))){
				return false;
			}
			return true
		},
		save(){
			let that=this
			if(that.isCheck){
				if(that.businessLicence&&that.idCardA&&that.idCardB&&that.societyId&&that.bankEnrollName&&that.mainBankNum&&that.bankName&&that.takeAddress&&that.takeContactsName&&that.takeContactsPhone){
					if(!that.checkPhone(that.takeContactsPhone)){
						uni.showToast({
							title:"请填写正确的手机号码",
							icon:"none"
						})
						return
					}
					let obj={
						accountName:that.prevObj.accountName,
						address:that.prevObj.address,
						bankCity:that.bankC[that.bpCndex].code,
						bankEnrollName:that.bankEnrollName,
						bankName:that.bankName,
						bankProvince:that.bankP[that.bpIndex].code,
						businessLicence:that.businessLicence,
						checkNote:that.checkNote,
						companyArea:that.prevObj.companyArea,
						companyCity:that.prevObj.companyCity,
						companyName:that.prevObj.companyName,
						companyProvince:that.prevObj.companyProvince,
						contactsName:that.prevObj.contactsName,
						contactsPhone:that.prevObj.contactsPhone,
						existsShop:that.prevObj.existsShop,
						latitude:that.prevObj.latitude,//纬度
						longitude:that.prevObj.longitude,//经度
						displayImg:that.prevObj.displayImg,
						idCardA:that.idCardA,
						idCardB:that.idCardB,
						isFront:that.prevObj.isFront,
						linePayRatio:that.prevObj.linePayRatio,
						mainBankNum:that.mainBankNum,
						id:that.shopObj.merchantId,
						marchantId:that.marchantId,
						marchantType:that.prevObj.marchantType,
						qq:that.prevObj.qq,
						servicePhone:that.prevObj.servicePhone,
						shopName:that.prevObj.shopName,
						societyId:that.societyId,
						takeAddress:that.takeAddress,
						takeArea:that.takeX[that.txIndex].code,
						takeCity:that.takeC[that.tcIndex].code,
						takeContactsName:that.takeContactsName,
						takeContactsPhone:that.takeContactsPhone,
						takeProvince:that.takeP[that.tpIndex].code,
						weixin:that.prevObj.weixin,
						level:that.shopObj.level	
					}
					that.$ajax({
						url:"/channelManagementRest/addOrUpdateMarchantInfo",
						data:obj,
						success(d){
							var title="申请成功"
							if(that.marchantId){
								title="修改成功"
							}
							uni.showToast({
								title:title,
								icon:"none"
							}),
							setTimeout(function(){
								uni.navigateBack({
									delta:2
								})
							},2000)
						}
					})
					
				}else{
					uni.showToast({
						title:"请填写完整的信息",
						icon:"none"
					})
				}
			}else{
				uni.showToast({
					title:"请同意协议"
				})
			}
		},
		bankPChange(e){
			this.bpIndex=e.detail.value;
			this.searchCity(1)
		},
		bankChange(e){
			this.bpCndex=e.detail.value;
		},
		takePChange(e){
			this.tpIndex=e.detail.value;
			this.searchCity(2)
		},
		takeCChange(e){
			this.tcIndex=e.detail.value;
		},
		takeXChange(e){
			this.txIndex=e.detail.value;
		},
		//查询市
		searchCity(type,def,obj){
			let that=this;
			var code="";
			if(type==1){
				//银行
				code=that.bankP[that.bpIndex].code
			}else{
				//退货
				code=that.takeP[that.tpIndex].code
			}
			that.$ajax({
				url:"/shopProduct/getCommonAddressCityList",
				data:{
					proCode:code
				},
				success(d){
					if(def){
						for(let i in d){
							if(that.prevObj.companyCity==d[i].code){
								that.tcIndex=i;
							}
						}
					}
					if(obj){
						console.log("obj",d)
						for(let i in d){
							if(obj.takeCity==d[i].code){
								that.tcIndex=i
							}
							if(obj.bankCity==d[i].code){
								that.bpCndex=i
							}
						}
					}
					if(type==1){
						that.bankC=d;
					}else if(type==2){
						that.takeC=d;
						that.searchX(def,obj);
					}else{
						that.bankC=d;
						that.takeC=d;
						that.searchX(def,obj);
					}
					
				}
			})
		},
		//查询区
		searchX(def,obj){
			let that=this;
			that.$ajax({
				url:"/shopProduct/getCommonAddressDistrictList",
				data:{
					cityCode:that.takeC[that.tcIndex].code
				},
				success(d){
					that.takeX=d;
					if(def){
						for(let i in d){
							if(that.prevObj.companyArea==d[i].code){
								that.txIndex=i;
							}
						}
					}
					if(obj){
						for(let i in d){
							if(obj.takeArea==d[i].code){
								that.txIndex=i;
							}
						}
					}
				}
			})
		},
		//同公司地址
		voluation(){
			let that=this;
			console.log(that.prevObj);
			for(let i in that.takeP){
				if(that.prevObj.companyProvince==that.takeP[i].code){
					that.tpIndex=i;
					that.takeContactsName=that.prevObj.contactsName,//收货联系人
					that.takeAddress=that.prevObj.address,//收货地址
					that.takeContactsPhone=that.prevObj.contactsPhone,//收货联系电话
					that.searchCity(2,1);
				}
			}
		},
		//是否同意协议
		checkboxChange(e){
			if(e.detail.value.length>0){
				this.isCheck=true;
			}else{
				this.isCheck=false;
			}
		},
		limit(limit){
			var size = "";
			size = (limit/(1024 * 1024)).toFixed(2);//转MB
			var sizeStr = size + "";                        //转成字符串
			var index = sizeStr.indexOf(".");                    //获取小数点处的索引
			var dou = sizeStr.substr(index + 1 ,2)            //获取小数点后两位的值
			if(dou == "00"){                                //判断后两位是否为00，如果是则删除00
				return sizeStr.substring(0, index) + sizeStr.substr(index + 3, 2)
			}
			return size;
		},
		//店铺图片选择
		proImgsChoose(type){
			let that=this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: function (res) {
					if(that.limit(res.tempFiles[0].size)>1){
						uni.showToast({
							title:"图片大小不能超过1M",
							icon:"none"
						})
						return
					}
					that.$ajax({
						uploadFile:true,
						formData:{
							'fileName':"/marchant/",
						},
						filePath:res.tempFilePaths[0],
						success(d){
							if(type==1){
								that.businessLicence=d
							}else if(type==2){
								that.idCardB=d
							}else{
								that.idCardA=d
							}
						}
					})
					// //转base64
					// pathToBase64(res.tempFilePaths[0])
					//   .then(base64 => {
					// 	  that.uploadImg(base64,type)
					//   })
					//   .catch(error => {
					// 	console.error(error)
					//   })
				}
			});
		},
		//店铺图片上传
		uploadImg(base64,type){
			let that=this;
			that.$ajax({
				url:"/commonUpload/upload",
				method:"POST",
				data:{
					base64Img:base64,
					fileName:"/marchant/",
					artworkMaster:1
				},success(d){
					if(type==1){
						that.businessLicence=d
					}else if(type==2){
						that.idCardB=d
					}else{
						that.idCardA=d
					}
				}
			})
		},
		url(){
			console.log(0)
			uni.navigateTo({
				url:"web_view"
			})
			return false
		}
	},
	onLoad(e){
		let that=this;
		let obj=JSON.parse(e.obj);
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
		this.prevObj=obj;
		if(e.marchantId){
			that.marchantId=e.marchantId;
			that.$ajax({
				url:"/channelManagementRest/getSelectMarchantInfo",
				data:{
					marchantId:that.marchantId
				},success(d){
					that.businessLicence=d.businessLicence;
					that.idCardB=d.idCardB;
					that.idCardA=d.idCardA;
					that.societyId=d.societyId;
					that.bankEnrollName=d.bankEnrollName;
					that.mainBankNum=d.mainBankNum;
					that.bankName=d.bankName;
					that.takeAddress=d.takeAddress;
					that.takeContactsName=d.takeContactsName;
					that.takeContactsPhone=d.takeContactsPhone;
					that.checkNote=d.checkNote;
					that.provinceList(d)
				}
			})
		}else{
			that.provinceList()
		}
		
	}
}
